
.and-or-rule {
  position: relative;
  height: 30px;
  margin-left: 15px !important;
  padding-left: 0;
}
.and-or-rule:before,
.and-or-rule:after {
  content: '';
  position: absolute;
  left: -1px;
  width: 16px;
  height: calc(50% + 15px);
  border-color: #c0c5e2;
  border-style: solid;
  z-index: -1;
}
.and-or-rule:before {
  top: -15px;
  border-width: 0 0 2px 2px;
}
.and-or-rule:after {
  top: 50%;
  border-width: 0 0 0 2px;
}
.and-or-rule:last-child:after {
  border: none;
}



