
.and-or-template {
  padding: 8px;
  position: relative;
  border-radius: 3px;
  border: 1px solid #6d77b8;
  border-top: 3px solid #d2d6de;
  margin-bottom: 20px;
  /* width: 100%; */
  box-shadow: 0 1px 1px rgba(0,0,0,0.1);
  border-top-color: #6d77b8;
  background-color: rgba(217, 222, 255, 0.05);
}
.and-or-template:before,
.and-or-template:after {
  content: '';
  position: absolute;
  left: -17px;
  width: 16px;
  height: calc(50% + 18px);
  border-color: #c0c5e2;
  border-style: solid;
  z-index: -1;
}
.and-or-template:before {
  top: -18px;
  border-width: 0 0 2px 2px;
}
.and-or-template:after {
  top: 50%;
  border-width: 0 0 0 2px;
}
.and-or-first:before,
.and-or-first:after,
.and-or-template:last-child:after {
  border: none;
}
.and-or-top,
.btn-and-or {
  padding: 0;
}
.btn-and-or button {
  margin-left: 4px;
}
.and-or-offset {
  margin-left: 30px;
}



